h1, h2, h3, h4, div, a {
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

.header {
  color: #ccc;
  background-color: #a8323c;
  grid-template-columns: repeat(12, 1fr);
  height: auto;
  min-height: 10vh;
  display: grid;
}

.header .header_titulo {
  background-color: red;
  grid-column: 2 / 9;
  height: 100%;
}

.header .header_nav {
  background-color: #00f;
  grid-column: 9 / 12;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.header .header_nav .nav_hamburger {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header .header_nav .nav_span {
  cursor: pointer;
  background-color: #000;
  border-radius: 2px;
  width: 30px;
  height: 4px;
  margin: 2px;
  transition: all .5s ease-in;
}

.header .nav_hamburger:hover .nav_span:nth-child(2) {
  background-color: red;
  transform: scale(.8);
}

